
import { useContext } from 'react';
import { Col, Row,Image ,Button} from 'react-bootstrap';
import KidsGalleryNavBar from '../../components/KidsGalleryNavBar/KidsGalleryNavBar';
import ActiveUserContext from '../../shared/ActiveUserContext';
import './HomePage.css';

function HomePage(props) {
    const {onLogout} = props;
    const activeUser= useContext(ActiveUserContext);

    return (
        <div className="p-home">
            <KidsGalleryNavBar onLogout={onLogout}></KidsGalleryNavBar>
            <div className="homeDiv">
                <Row >
                <Col  lg={6} md={12}  >
                    <div className="about-card">
                        <h2 className="about-content1">Your kids came back from kindergarten or school with a beautiful artwork that you want to save or share with others? </h2>
                        <div className="about-content2" >
                            Now you can store all this artworks in one palce!
                        </div>
                    </div>
                    {/* <div className="about">HomePage</div> */}
                    </Col>
                    <Col lg={6} md={12} >
                     <Row l={12}>
                        <Image src="data:image/jpeg;base64,"></Image>
                        </Row>
                        <Row>
                            <Button className="join" href="#/SignUp"> Join us!</Button>
                        </Row>
                  
                    </Col>
                  
                </Row>
                
            </div>
            
        </div>
    );
}

export default HomePage;